body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.edit-name-box {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    background: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.edit-name-box .ant-form {
    padding: 40px !important;
    border: 1px solid #cecccc;
    border-radius: 2px;
}
#root {
    height: inherit;
}
.wrapper {
    padding: 24px;
    background: #d6d6d6;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: inherit;
}
.fulu-box {
    padding: 24px;
    background: #fff;
}
.form-box .btn-collapse {
    margin-left: 8px;
    font-size: 12px;
    text-decoration: none;
}
.table-box {
    margin-top: 24px;
    -webkit-flex: 1 1;
            flex: 1 1;
}
.table-box .ant-table-thead td {
    background: #d6d6d6;
}
.fulu-box .ant-form-item {
    margin-bottom: 14px !important;
    margin-right: 0;
    display: -webkit-flex;
    display: flex;
}
.fulu-box .ant-form-item-control-wrapper {
    -webkit-flex: 1 1;
            flex: 1 1;
}
.btn-group-box button:not(:first-child) {
    margin-left: 5px;
}
.btn-group-box a:not(:first-child) {
    margin-left: 5px;
}
.fulu-cell-box {
    position: relative;
    background: #fff;
    padding: 24px;
    font-size: 0;
}
.fulu-cell-box:after {
    content: '';
    display: table;
    clear: both;
}
.fulu-cell-box .cell {
    float: left;
    min-height: inherit;
    font-size: 14px;
}
.btn-box {
    text-align: center;
    margin: 10px 0;
}
.btn-bottom-box {
    position: absolute;
    right: 10px;
    bottom: 0;
    padding: 10px 16px;
    text-align: right;
}
.form-drop-container {
    position: relative;
    display: inline-block;
    min-height: 42px;
    outline: 1px dashed #c7c8ca;
    text-align: center;
}
.form-drop-container>div {
    width: 100%;
}
.form-drop-container:hover {
    border-color: #1890ff;
}
.form-drop-container:hover>.icon-operation {
    display: block;
}
.container .form-drop-container>.icon-operation {
    right: 0;
    top: 0;
    display: none;
}
.form-container {
    font-size: 0;
}
.form-container .inner-box {
    min-height: 120px;
    padding: 24px;
    background-color: #fff;
}
.form-container .ant-form {
    text-align: left;
    padding: 0 24px;
}
.btn-add-box {
    text-align: center;
    margin: 10px 0 25px;
}

.ant-row.ant-form-item {
    margin-bottom: 0;
}
.panel-header .btn-delete {
    float: right;
    margin: 5px;
}
.ant-table {
    cursor: pointer;
}
.table-container {
    background: #fff;
}
.table-container table .ant-table-thead th {
    padding-top: 9px;
    padding-bottom: 9px; 
}

