}
.preview-mode {
    margin: 24px;
    min-height: 40px;
}
.preview-mode .drop-container>div {
    text-align: left;
}
.preview-mode .cell {
    outline: 1px dashed #d9d9d9;
}
.layout-container {
    cursor: pointer;
    min-height: 100px;
    border: 1px dashed #ccc;
    margin-bottom: 10px;
    padding: 5px 24px;
    font-size: 0;
}
.layout-container .ant-form {
    text-align: left;
}
.empty-tips {
    position: absolute;
    left: 50%;
    top: 50%;
    border: 2px dashed #ccc;
    line-height: 50px;
    padding: 0 5px;
    margin-left: -56px;
    margin-top: -27px;
}
.main-container {
	position: relative;
	margin: 10px 0;
	background-color: #e4e4e4;
	min-height: 300px;
	border: 1px dashed #1890ff;
	overflow: hidden;
}
.ant-form:after {
	content: '';
	display: table;
	clear: both;
}
#root {
  height: inherit;
}
.App {
  text-align: center;
  height: inherit;
  display: -webkit-flex;
  display: flex;
}
.ant-layout-sider {
  background-color: #fff !important;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.drag-item {
  display: inline-block;
  border: 1px solid #cccccc;
  padding: 0 5px;
  cursor: move;
  margin: 5px;
}
.drag-item:hover {
  border-color: #1890ff;
  color: #1890ff;
}

.container {
  position: relative;
  margin: 24px;
}

.container .icon-operation, .fulu-cell-box .icon-operation {
  position: absolute;
  right: 0;
  top: 0;
  display: none;
  font-size: 14px;
}
.container .icon-operation.anticon-close-circle {
  top: -6px;
  right: -6px;
}
.container:hover .icon-operation, .fulu-cell-box:hover .icon-operation {
  display: block;
}

.ant-form-item {
  margin-bottom: 24px;
  margin-right: 0;
  display: -webkit-flex;
  display: flex;
}
.ant-form-item-control-wrapper {
  -webkit-flex: 1 1;
          flex: 1 1;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

