.edit-name-box {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.edit-name-box .ant-form {
    padding: 40px !important;
    border: 1px solid #cecccc;
    border-radius: 2px;
}